.footer {
  background: #2c3e50;
  color: white;
  padding: 10px 0 6px;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 0.5rem;
  margin-bottom: 6px;
}

.footer-section h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #667eea;
  font-weight: 600;
}

.footer-section h4 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #667eea;
  font-weight: 600;
}

.footer-section p {
  color: #bdc3c7;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-section ul li {
  margin-bottom: 0.5rem;
}

.footer-section ul li a {
  color: #bdc3c7;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-section ul li a:hover {
  color: #667eea;
}

.social-links {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.social-links a {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: rgba(102, 126, 234, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: all 0.3s ease;
}

.social-links a:hover {
  background: #667eea;
  transform: translateY(-2px);
}

.social-links span {
  font-size: 1.2rem;
}

.contact-info p {
  margin-bottom: 0.5rem;
  color: #bdc3c7;
}

.footer-flex {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.footer-main {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  gap: 0.4rem;
  border-bottom: 1px solid #34495e;
  padding-bottom: 0.2rem;
}

.footer-brand {
  flex: 2;
  min-width: 220px;
}

.footer-logo-text {
  font-family: 'Komika Axis', Arial, sans-serif;
  color: #2c5aa0; /* Changed from #60b583 to professional blue */
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 0.92rem;
  font-weight: 700;
  display: block;
  margin-bottom: 0;
}

.footer-tagline {
  color: #bdc3c7;
  font-size: 0.9rem;
  margin-bottom: 0;
}

.footer-contact {
  flex: 1;
  min-width: 180px;
  color: #bdc3c7;
  font-size: 0.9rem;
  margin-bottom: 0;
}

.footer-contact a {
  color: #2c5aa0; /* Changed from #60b583 to professional blue */
  text-decoration: none;
  font-weight: 600;
}

.footer-contact a:hover {
  text-decoration: underline;
}

.footer-social {
  display: flex;
  gap: 0.2rem;
  margin-top: 0;
}

.footer-social a {
  display: inline-block;
  width: 36px;
  height: 36px;
  background: rgba(44, 90, 160, 0.12); /* Changed from green to blue */
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: all 0.3s ease;
  color: #2c5aa0; /* Changed from #60b583 to professional blue */
  font-size: 1.3rem;
  display: flex;
}

.footer-social a:hover {
  background: #2c5aa0; /* Changed from #60b583 to professional blue */
  color: #fff;
}

.footer-bottom {
  padding-top: 2px;
  margin-top: 0;
}

.footer-bottom-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.1rem;
}

.footer-links {
  display: flex;
  gap: 1.2rem;
}

.footer-links a {
  color: #bdc3c7;
  text-decoration: none;
  font-size: 0.95rem;
  transition: color 0.2s;
}

.footer-links a:hover {
  color: #60b583;
}

.footer-copyright {
  color: #bdc3c7;
  font-size: 0.85rem;
}

/* Remove .footer-links-row styles */
.footer-links-row {
  display: none !important;
}

.footer-links-row a {
  color: #2c5aa0; /* Changed from #60b583 to professional blue */
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  letter-spacing: 1px;
  transition: color 0.2s;
}

.footer-links-row a:hover {
  text-decoration: underline;
  color: #1e4a7a; /* Darker blue for hover */
}

@media (max-width: 900px) {
  .footer-main {
    flex-direction: column;
    gap: 0.2rem;
    align-items: flex-start;
  }
  .footer-bottom-content {
    gap: 0.05rem;
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 6px 0 4px;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 0.2rem;
  }
  
  .footer-bottom-content {
    flex-direction: column;
    text-align: center;
  }
  
  .footer-links {
    justify-content: center;
  }
} 